import {  
    mapState,
	mapMutations  
} from 'vuex';  
import {web} from "../../../util/config.js"
export default {
	data() {
		return {
			 date: '',
			 sex:'男',
			 index: 0,
			 array:['男','女']
		}
	},
	computed: {
		...mapState(['hasLogin','userInfo']),
		 startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	onLoad() {
		let self=this;
		self.date=self.userInfo.birthday;
		self.sex=(self.userInfo.gender==1?'男':'女');
		self.index=(self.userInfo.gender==1?0:1);
		uni.showLoading({
			title:'正在请求'
		});
		self.$axios(self.$baseUrl.getUserInfo,{})
		.then(res=>{
			uni.hideLoading();
			console.log(res);
			if(res.data.code==1){
				self.userInfo.birthday=(res.data.data.birthday==''||res.data.data.birthday==undefined)?'未填写':res.data.data.birthday
				self.userInfo.gender=res.data.data.gender;
				self.date=self.userInfo.birthday;
				self.sex=(self.userInfo.gender==1?'男':'女');
				self.index=(self.userInfo.gender==1?0:1);
			}else{
				self.$api.msg(res.data.msg);
			}
		},(error) =>{
			uni.hideLoading();
			console.log(error)
		})	
	},
	methods: {
		 ...mapMutations(["login"]),
		 bindDateChange: function(e) {
			this.date = e.target.value;
			console.log(this.date+'================================');
			this.proBirthday();
		},
		bindSexChange: function(e) {
			this.sex = this.array[e.target.value];
			console.log(this.sex+'================================');
			this.userInfo.gender=(this.sex=='男'?1:2);
			this.index=(this.sex=='男'?0:1);
			//可以适当的判断下
			this.proSex(this.userInfo.gender);
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		changeHeader(){
			let self=this;
			uni.chooseImage({
				success(res) {
					console.log(res);
					console.log(res.tempFilePaths[0]);
					uni.showLoading({
						title: '正在上传',
					});
					uni.uploadFile({
						url: web.www+'/api.user/upload', //仅为示例，非真实的接口地址
						filePath: res.tempFilePaths[0],
						name: 'file',
						header: {  
							"Content-Type": "multipart/form-data"  
						},
						formData: {
							'token': self.userInfo.token
						},
						success: (uploadFileRes) => {
							uni.hideLoading();
							console.log(uploadFileRes.data);
							var jsonStr = uploadFileRes.data.replace(" ", "");
							if (typeof jsonStr != 'object') {
								jsonStr = jsonStr.replace(/\ufeff/g, ""); //字符串转化JSON对象
								var jj = JSON.parse(jsonStr);
								self.userInfo.portrait=jj.data.url;
								self.profileAvatar(self.userInfo.portrait);								
							}  
						},
						fail(res) {
							console.log(res);
							uni.hideLoading();
						}
					});
				}
			})
		},
		profileAvatar(avatar){
			//修改个人信息图片
			uni.showLoading({
				title: '正在提交',
			});
			let self=this;
			self.$axios(self.$baseUrl.profile,
			{avatar:avatar,
			nickname:self.userInfo.nickname,
			gender:self.userInfo.gender,
			birthday:self.userInfo.birthday,
			})
			.then(res =>{
					uni.hideLoading();
					console.log(res);
					if(res.data.code == 1){
						console.log(avatar)
						console.log('修改个人信息图片成功===============')
						self.userInfo.portrait=avatar;
						self.login(self.userInfo);
					}else{
						self.$api.msg(res.data.msg);
					}
				},(error) =>{
					uni.hideLoading();
					console.log(error)
				})
		}
		,toPage(url){
			uni.navigateTo({  
				url
			})  
		},
		proBirthday(){
			uni.showLoading({
				title: '正在提交',
			});
			let self=this;
			self.$axios(self.$baseUrl.profile,
			{avatar:self.userInfo.portrait,
			nickname:self.userInfo.nickname,
			gender:self.userInfo.gender,
			birthday:self.date,
			})
			.then(res =>{
					uni.hideLoading();
					console.log(res);
					if(res.data.code == 1){
						console.log(self.date)
						console.log('修改个人生日成功===============')
						self.userInfo.birthday=self.date;
						self.login(self.userInfo);
					}else{
						self.$api.msg(res.data.msg);
					}
				},(error) =>{
					uni.hideLoading();
					console.log(error)
				})
		},
		proSex(gender){
			uni.showLoading({
				title: '正在提交',
			});
			let self=this;
			self.$axios(self.$baseUrl.profile,
			{avatar:self.userInfo.portrait,
			nickname:self.userInfo.nickname,
			gender:gender,
			birthday:self.userInfo.birthday,
			})
			.then(res =>{
					uni.hideLoading();
					console.log(res);
					if(res.data.code == 1){
						console.log(gender)
						console.log('修改个人性别成功===============')
						self.userInfo.gender=gender;
						self.login(self.userInfo);
					}else{
						self.$api.msg(res.data.msg);
					}
				},(error) =>{
					uni.hideLoading();
					console.log(error)
				})
		}
	}
}
